.header {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 70px;
  background-color: #262626;
  z-index: 2;
}

.container {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 80%;
  height: 80%;
  border: 1px solid;
}




