.slogan {
  height: 250px;
  background-color: #252527;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.container {
  width: 80%;
  height: 80%;
  border: 1px solid;
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  align-items: center;
}

button {
  width: 20%;
  height: 30px;
}

h2 {
  text-align: center;
}

@media screen and (max-width: 575px) {
  .container {
    width: 100%;
    align-items: center;
  }
}